import { memo, useState, useMemo, useCallback } from 'react';
import { formatNumber, formatPercent, formatCompactCurrency } from '../utils/formatters';

/**
 * 한국 지도 SVG 히트맵 컴포넌트
 * 17개 시도를 시각화하고 데이터에 따라 색상을 매핑
 */

// 시도별 SVG 경로 데이터 (간소화된 형태)
const REGION_PATHS = {
    seoul: {
        path: "M 195 155 L 210 150 L 220 160 L 215 175 L 200 180 L 190 170 Z",
        center: [205, 165],
        name: "서울",
    },
    gyeonggi: {
        path: "M 150 120 L 200 100 L 250 120 L 260 180 L 230 220 L 180 240 L 130 200 L 140 150 Z M 195 155 L 210 150 L 220 160 L 215 175 L 200 180 L 190 170 Z",
        center: [195, 170],
        name: "경기",
        fillRule: "evenodd",
    },
    incheon: {
        path: "M 120 140 L 150 130 L 155 160 L 140 180 L 115 170 Z",
        center: [135, 155],
        name: "인천",
    },
    gangwon: {
        path: "M 250 80 L 340 60 L 370 120 L 350 200 L 280 220 L 250 180 L 260 120 Z",
        center: [300, 140],
        name: "강원",
    },
    chungbuk: {
        path: "M 230 200 L 280 190 L 310 230 L 290 280 L 240 290 L 220 250 Z",
        center: [260, 240],
        name: "충북",
    },
    chungnam: {
        path: "M 120 200 L 180 190 L 220 230 L 200 300 L 140 320 L 100 280 L 90 240 Z",
        center: [155, 260],
        name: "충남",
    },
    sejong: {
        path: "M 185 230 L 205 225 L 210 245 L 195 255 L 180 245 Z",
        center: [195, 240],
        name: "세종",
    },
    daejeon: {
        path: "M 195 260 L 220 255 L 225 280 L 205 290 L 190 280 Z",
        center: [207, 272],
        name: "대전",
    },
    jeonbuk: {
        path: "M 100 300 L 180 290 L 200 340 L 160 400 L 90 380 L 80 340 Z",
        center: [140, 345],
        name: "전북",
    },
    gwangju: {
        path: "M 115 410 L 140 405 L 145 430 L 125 440 L 110 425 Z",
        center: [127, 420],
        name: "광주",
    },
    jeonnam: {
        path: "M 50 380 L 110 370 L 150 400 L 170 460 L 130 510 L 60 490 L 40 440 Z",
        center: [100, 440],
        name: "전남",
    },
    gyeongbuk: {
        path: "M 280 200 L 360 180 L 380 250 L 360 330 L 300 360 L 260 320 L 270 260 Z",
        center: [320, 270],
        name: "경북",
    },
    daegu: {
        path: "M 295 320 L 325 315 L 330 345 L 310 355 L 290 340 Z",
        center: [310, 335],
        name: "대구",
    },
    ulsan: {
        path: "M 355 345 L 385 340 L 390 375 L 365 385 L 350 365 Z",
        center: [370, 360],
        name: "울산",
    },
    busan: {
        path: "M 325 385 L 365 380 L 375 420 L 345 435 L 320 410 Z",
        center: [345, 405],
        name: "부산",
    },
    gyeongnam: {
        path: "M 200 350 L 280 340 L 320 380 L 300 450 L 220 470 L 170 420 L 180 380 Z",
        center: [245, 400],
        name: "경남",
    },
    jeju: {
        path: "M 90 540 L 170 530 L 180 570 L 150 590 L 80 580 L 70 555 Z",
        center: [125, 560],
        name: "제주",
    },
};

// 색상 스케일 함수 (가격지수 기반)
const getColorScale = (value, min, max) => {
    const normalized = (value - min) / (max - min);

    // 파란색(하락) -> 회색(보합) -> 빨간색(상승)
    if (normalized < 0.5) {
        const t = normalized * 2;
        return `rgb(${Math.round(59 + t * (156 - 59))}, ${Math.round(130 + t * (163 - 130))}, ${Math.round(246 + t * (175 - 246))})`;
    } else {
        const t = (normalized - 0.5) * 2;
        return `rgb(${Math.round(156 + t * (239 - 156))}, ${Math.round(163 + t * (68 - 163))}, ${Math.round(175 + t * (68 - 175))})`;
    }
};

// 변화율 기반 색상
const getChangeRateColor = (changeRate) => {
    if (changeRate > 2) return '#ef4444'; // 강한 상승
    if (changeRate > 0.5) return '#f97316'; // 상승
    if (changeRate > 0) return '#84cc16'; // 약한 상승
    if (changeRate === 0) return '#9ca3af'; // 보합
    if (changeRate > -0.5) return '#60a5fa'; // 약한 하락
    if (changeRate > -2) return '#3b82f6'; // 하락
    return '#1d4ed8'; // 강한 하락
};

const KoreaMapSVG = memo(({
    data = [],
    onRegionClick,
    selectedRegion,
    colorBy = 'changeRate', // 'changeRate' | 'currentIndex' | 'avgPrice'
}) => {
    const [hoveredRegion, setHoveredRegion] = useState(null);
    const [tooltipPos, setTooltipPos] = useState({ x: 0, y: 0 });

    // 데이터를 지역 ID로 매핑
    const dataMap = useMemo(() => {
        const map = {};
        data.forEach(region => {
            map[region.id] = region;
        });
        return map;
    }, [data]);

    // min/max 계산 (색상 스케일용)
    const { minValue, maxValue } = useMemo(() => {
        if (data.length === 0) return { minValue: 0, maxValue: 100 };

        const values = data.map(d => d[colorBy]);
        return {
            minValue: Math.min(...values),
            maxValue: Math.max(...values),
        };
    }, [data, colorBy]);

    // 지역 색상 결정
    const getRegionColor = useCallback((regionId) => {
        const regionData = dataMap[regionId];
        if (!regionData) return '#d1d5db';

        if (colorBy === 'changeRate') {
            return getChangeRateColor(regionData.changeRate);
        }
        return getColorScale(regionData[colorBy], minValue, maxValue);
    }, [dataMap, colorBy, minValue, maxValue]);

    // 마우스 이벤트 핸들러
    const handleMouseEnter = useCallback((regionId, e) => {
        setHoveredRegion(regionId);
        const rect = e.currentTarget.closest('svg').getBoundingClientRect();
        setTooltipPos({
            x: e.clientX - rect.left,
            y: e.clientY - rect.top,
        });
    }, []);

    const handleMouseMove = useCallback((e) => {
        const rect = e.currentTarget.closest('svg').getBoundingClientRect();
        setTooltipPos({
            x: e.clientX - rect.left,
            y: e.clientY - rect.top,
        });
    }, []);

    const handleMouseLeave = useCallback(() => {
        setHoveredRegion(null);
    }, []);

    // 툴팁 데이터
    const tooltipData = useMemo(() => {
        if (!hoveredRegion) return null;
        return dataMap[hoveredRegion];
    }, [hoveredRegion, dataMap]);

    return (
        <div style={{ position: 'relative', width: '100%', height: '100%' }}>
            <svg
                viewBox="0 0 420 620"
                style={{ width: '100%', height: '100%' }}
            >
                {/* 배경 */}
                <rect x="0" y="0" width="420" height="620" fill="transparent" />

                {/* 지역 렌더링 */}
                {Object.entries(REGION_PATHS).map(([regionId, regionInfo]) => {
                    const isHovered = hoveredRegion === regionId;
                    const isSelected = selectedRegion === regionId;
                    const fillColor = getRegionColor(regionId);

                    return (
                        <g key={regionId}>
                            <path
                                d={regionInfo.path}
                                fill={fillColor}
                                fillRule={regionInfo.fillRule || 'nonzero'}
                                stroke={isSelected ? '#6366f1' : isHovered ? '#4b5563' : '#9ca3af'}
                                strokeWidth={isSelected ? 3 : isHovered ? 2 : 1}
                                style={{
                                    cursor: 'pointer',
                                    transition: 'all 0.2s ease',
                                    opacity: isHovered || isSelected ? 1 : 0.85,
                                    filter: isHovered ? 'brightness(1.1)' : 'none',
                                }}
                                onMouseEnter={(e) => handleMouseEnter(regionId, e)}
                                onMouseMove={handleMouseMove}
                                onMouseLeave={handleMouseLeave}
                                onClick={() => onRegionClick?.(regionId)}
                            />
                            {/* 지역명 라벨 */}
                            <text
                                x={regionInfo.center[0]}
                                y={regionInfo.center[1]}
                                textAnchor="middle"
                                fontSize="10"
                                fontWeight="600"
                                fill="#1f2937"
                                style={{ pointerEvents: 'none' }}
                            >
                                {regionInfo.name}
                            </text>
                        </g>
                    );
                })}
            </svg>

            {/* 툴팁 */}
            {tooltipData && (
                <div
                    style={{
                        position: 'absolute',
                        left: tooltipPos.x + 10,
                        top: tooltipPos.y - 10,
                        background: 'var(--color-bg-primary)',
                        border: '1px solid var(--color-border)',
                        borderRadius: 'var(--radius-md)',
                        padding: '12px 16px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
                        zIndex: 100,
                        pointerEvents: 'none',
                        minWidth: 150,
                    }}
                >
                    <div style={{
                        fontWeight: 600,
                        marginBottom: 8,
                        fontSize: '0.95rem',
                        color: 'var(--color-text-primary)',
                    }}>
                        {tooltipData.name}
                    </div>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: 4 }}>
                        <div style={{ display: 'flex', justifyContent: 'space-between', gap: 16 }}>
                            <span style={{ color: 'var(--color-text-tertiary)', fontSize: '0.8rem' }}>지수</span>
                            <span style={{ fontWeight: 500, fontSize: '0.85rem' }}>{tooltipData.currentIndex}</span>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', gap: 16 }}>
                            <span style={{ color: 'var(--color-text-tertiary)', fontSize: '0.8rem' }}>변화율</span>
                            <span style={{
                                fontWeight: 600,
                                fontSize: '0.85rem',
                                color: tooltipData.changeRate >= 0 ? 'var(--color-success)' : 'var(--color-danger)',
                            }}>
                                {formatPercent(tooltipData.changeRate)}
                            </span>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', gap: 16 }}>
                            <span style={{ color: 'var(--color-text-tertiary)', fontSize: '0.8rem' }}>평균가</span>
                            <span style={{ fontWeight: 500, fontSize: '0.85rem' }}>
                                {formatCompactCurrency(tooltipData.avgPrice)}
                            </span>
                        </div>
                    </div>
                </div>
            )}

            {/* 범례 */}
            <div style={{
                position: 'absolute',
                bottom: 16,
                right: 16,
                background: 'var(--color-bg-primary)',
                border: '1px solid var(--color-border)',
                borderRadius: 'var(--radius-md)',
                padding: '12px',
                fontSize: '0.75rem',
            }}>
                <div style={{ fontWeight: 600, marginBottom: 8, color: 'var(--color-text-secondary)' }}>
                    {colorBy === 'changeRate' ? '변화율' : colorBy === 'currentIndex' ? '지수' : '가격'}
                </div>
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                    {colorBy === 'changeRate' ? (
                        <>
                            <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                                <div style={{ width: 12, height: 12, borderRadius: 2, background: '#1d4ed8' }} />
                                <span>하락</span>
                            </div>
                            <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                                <div style={{ width: 12, height: 12, borderRadius: 2, background: '#9ca3af' }} />
                                <span>보합</span>
                            </div>
                            <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                                <div style={{ width: 12, height: 12, borderRadius: 2, background: '#ef4444' }} />
                                <span>상승</span>
                            </div>
                        </>
                    ) : (
                        <div style={{
                            width: 100,
                            height: 12,
                            borderRadius: 2,
                            background: 'linear-gradient(to right, #3b82f6, #9ca3af, #ef4444)',
                        }} />
                    )}
                </div>
            </div>
        </div>
    );
});

KoreaMapSVG.displayName = 'KoreaMapSVG';

export default KoreaMapSVG;
